<template lang="pug">
.curso-main-container.pb-3
  BannerInterno(:subTitulo="'2. Tipos de investigación: cualitativa, cuantitativa y mixta'")
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5

    .row.mb-4
      .col-12.col-xl-7(data-aos="fade-right")
        p.mb-4 Iniciamos este tema, desde una situación en contexto real. Supongamos que un equipo de investigadores quiere entender cómo el uso de redes sociales influye en la autoestima de los adolescentes. Según el tipo de investigación que se elija, el enfoque del estudio va a variar. 
        span Desde lo <strong>cuantitativo</strong>, en este caso, el equipo opta por medir la relación entre el tiempo que los adolescentes pasan en redes sociales y su nivel de autoestima. Desde lo <strong>cualitativo</strong>, aquí, los investigadores buscan explorar las experiencias y percepciones de los adolescentes sobre cómo las redes sociales afectan su autoestima. Y, por último, desde la investigación mixta, en este caso, el equipo combina ambos enfoques para obtener una visión más completa del impacto de las redes sociales en la autoestima.
      .d-none.d-xl-block.col-xl-5(data-aos="fade-left")
        img(src='@/assets/curso/unidad/img-6.png', alt='Texto que describa la imagen')

    .row.mb-4
      .d-none.d-xl-block.col-xl-5(data-aos="fade-right")
        img(src='@/assets/curso/unidad/img-7.png', alt='Texto que describa la imagen')
      .col-12.col-xl-7(data-aos="fade-left")
        p.mb-4 Los tipos de investigación, cualitativa, cuantitativa y mixta, se distinguen por sus métodos y enfoques para recolectar y analizar datos. Cada uno de estos tipos tiene sus propias fortalezas y es adecuado para diferentes objetivos de estudio.
        .d-flex.align-items-start.bg-rojo.p-4.mb-4(data-aos="fade-left")
          img.me-3(style="width: 90px" src='@/assets/curso/unidad/img-8.svg', alt='Texto que describa la imagen')
          .d-flex.flex-column
            p.pe-xl-3.mb-0(data-aos="fade-up") Las condiciones en las cuales se desarrolla la investigación, conducen a la elección de alguno de alguna tipología, las cuales son abordadas netamente desde lo metodológico y su relación con las diferentes áreas de conocimiento. 
        
    p.mb-4 Por lo general, en las teorías tradicionales, se acostumbra describir dos enfoques metodológicos: el cuantitativo y el cualitativo; sin embargo, ha ido emergiendo una opción metodológica que combina estos dos, el mixto, que ofrece ventajas y oportunidades para abordar problemas complejos y multifacéticos. (Aravena, Kimelman, Micheli, Torrealba, & Zúñiga, 2006). 
    .row.mb-4
      .col-12.col-xl-7.mx-auto(data-aos="fade-up")
        p.mb-3 <strong>Figura 1.</strong> Tipos de investigación
        img(src='@/assets/curso/unidad/img-9.png', alt='Texto que describa la imagen').d-none.d-md-block
        img(src='@/assets/curso/unidad/img-10.png', alt='Texto que describa la imagen').d-md-none

    #t_2_1.titulo-segundo
      div.d-flex
        span.me-3 2.1  
        h2 Investigación cualitativa

    p.mb-4 Antes de adentrarnos en las definiciones, vamos a partir de una situación; se quiere indagar en estudiantes universitarios que iniciaron sus carreras universitarias durante la pandemia y que ahora están a punto de graduarse o ya han culminado sus estudios, sobre “La experiencia de haber estudiado parte de su carrera durante la pandemia del COVID-19”. Se elige un grupo de estudiantes de diferentes carreras y años académicos, al igual que graduados, para obtener diversas perspectivas. Este grupo no tiene que ser grande; a menudo, en la investigación cualitativa, se trabaja con muestras más pequeñas, pero representativas. Todo el estudio se basa en las respuestas espontáneas a la pregunta: ¿Cómo cree que la pandemia del COVID-19 afectó a la vida académica y social de los estudiantes universitarios? 
    p.mb-4 Ahora, conozcamos un poco más sobre la investigación cualitativa:

    .row.mb-5
      .col-12.col-lg-7(data-aos="fade-right")
        AcordionA(tipo="b")
          .div(titulo="¿Qué es?")
            p La <strong>investigación cualitativa</strong> es un enfoque metodológico utilizado en las ciencias sociales para explorar y comprender fenómenos complejos que no pueden ser fácilmente cuantificados. A diferencia de la investigación cuantitativa, que se enfoca en la medición numérica y el análisis estadístico, la investigación cualitativa se centra en el análisis profundo de los comportamientos, emociones, y percepciones de las personas. Este enfoque permite a los investigadores obtener una comprensión más rica y detallada de los contextos sociales, culturales y humanos en los que se desarrollan los fenómenos de estudio.
          .div(titulo="Principal objetivo")
            p Uno de los principales objetivos de la investigación cualitativa es interpretar la realidad desde la perspectiva de los participantes, lo que implica un proceso más flexible y abierto que la investigación cuantitativa. La investigación cualitativa es esencial porque ayuda a entender el "porqué" detrás de las acciones y pensamientos de las personas. Esto es valioso en muchos campos, como la educación, la salud y el marketing. Por ejemplo, en educación, entender cómo los estudiantes aprenden y qué desafíos enfrentan, puede ayudar a los maestros a crear mejores estrategias de enseñanza.
          .div(titulo="¿Qué permite?")
            p La investigación cualitativa permite a las voces de las personas ser escuchadas. Normalmente, se centra en comunidades o grupos que pueden no ser representados en los datos cuantitativos. Esto ayuda a crear un panorama más completo de la situación. 
          .div(titulo="Rol del investigador cualitativo")
            p El investigador cualitativo adopta un rol interpretativo y reflexivo, lo que le permite adaptar las preguntas y técnicas de recolección de datos en función de lo que se va descubriendo durante el estudio. Este enfoque es especialmente útil cuando se exploran temas nuevos o poco investigados, y cuando se busca entender los significados y motivaciones detrás de ciertas acciones o comportamientos humanos.
      .col-12.col-lg-5.d-none.d-lg-block(data-aos="fade-left")
        img(src='@/assets/curso/unidad/img-11.png', alt='Texto que describa la imagen')
    .row.mb-5
      .col-12.col-lg-5.d-none.d-lg-block(data-aos="fade-right")
        img(src='@/assets/curso/unidad/img-12.png', alt='Texto que describa la imagen')
      .col-12.col-lg-7(data-aos="fade-left")
        p.mb-4 Las principales características de la investigación cualitativa, son:
        AcordionA(tipo="b")
          .div(titulo="Exploratoria y descriptiva")
            p Uno de los aspectos más importantes es su carácter exploratorio. Se utiliza con frecuencia en áreas donde no se dispone de suficiente información previa o en temas emergentes que necesitan ser descritos y entendidos en profundidad. Este enfoque permite la creación de nuevas teorías o la expansión de las existentes, mediante la comprensión detallada de los fenómenos en su contexto natural.
          .div(titulo="Contextual")
            p A diferencia de la investigación cuantitativa, que tiende a incomunicar variables y buscar relaciones causales; la investigación cualitativa se interesa por cómo los fenómenos sociales son influidos por su entorno. La contextualización es clave en este tipo de estudios, porque los investigadores reconocen que el significado de las acciones o experiencias humanas, puede variar considerablemente, según el contexto cultural, histórico o social en el que se producen.
          .div(titulo="Flexible y adaptativa")
            p La flexibilidad es otra característica fundamental. Los investigadores cualitativos no siguen un plan de investigación rígido. Más bien, adaptan su enfoque en función de lo que encuentran durante el proceso de recolección y análisis de datos. Este enfoque permite ajustar preguntas y técnicas, de acuerdo con las necesidades del estudio y las respuestas de los participantes, lo que enriquece la profundidad y la autenticidad de los hallazgos.
          .div(titulo="Múltiples métodos de recolección de datos")
            p Las técnicas cualitativas más comunes incluyen entrevistas en profundidad, grupos focales y observaciones participantes. Estos métodos permiten a los investigadores obtener datos enriquecidos y detallados, dado que se centran en la comprensión de las experiencias y percepciones individuales de los participantes. Muchas veces, los estudios cualitativos utilizan más de un método para garantizar una mayor validez de los resultados, mediante la triangulación de datos, lo que permite al investigador contrastar y corroborar la información obtenida de diferentes fuentes.

    p.mb-4 A continuación, conozcamos los tipos de diseño en la investigación cualitativa: 
    .row.mb-5.justify-content-center 
      .col-10.col-md-8.col-lg-6.col-xl-4.mb-4.mb-xl-0
        .custom-image-card.d-flex.flex-column.p-4.py-md-5.h-100
          img.mx-auto.mb-4(src='@/assets/curso/unidad/img-13.svg', style="width: 80px")
          p.text-bold.mb-4.text-center Investigación básica
          p.px-1.text-small.mb-0.d-flex.text-center Se refiere a investigaciones de corte teórico, buscan profundizar en temas desde su sustento epistemológico y no pueden ejecutarse casos prácticos de ningún modo.
      .col-10.col-md-8.col-lg-6.col-xl-4.mb-4.mb-xl-0
        .custom-image-card.d-flex.flex-column.p-4.py-md-5.h-100
          img.mx-auto.mb-4(src='@/assets/curso/unidad/img-14.svg', style="width: 80px")
          p.text-bold.mb-4.text-center Investigación aplicada
          p.px-1.text-small.mb-0.d-flex.text-center Busca responder a preguntas específicas para resolver un problema particular que puede ser, por ejemplo, de tipo cultural u organizativo, con el ánimo de mejorar la  calidad de vida de la sociedad.
      .col-10.col-md-8.col-lg-6.col-xl-4
        .custom-image-card.d-flex.flex-column.p-4.py-md-5.h-100
          img.mx-auto.mb-4(src='@/assets/curso/unidad/img-15.svg', style="width: 80px")
          p.text-bold.mb-4.text-center Investigación descriptiva
          p.px-1.text-small.mb-0.d-flex.text-center Se fundamenta en estudios que requieren un mayor nivel de profundidad a partir del análisis de datos con el objetivo de mostrar características y propiedades de un fenómeno o problema.

    .row.mb-4
      .col-12.col-xl-7(data-aos="fade-right")
        p.mb-4 En la investigación cualitativa, los diseños de investigación se centran en la comprensión profunda de fenómenos complejos y subjetivos, explorando significados, experiencias y percepciones. A diferencia de la investigación cuantitativa, no se busca medir ni cuantificar variables, sino describirlas, interpretarlas y comprenderlas, desde una perspectiva más rica y contextual. 
        .bg-amarillo.p-4
          p.mb-4 En la investigación cualitativa, existen diversos diseños que permiten explorar fenómenos sociales desde perspectivas únicas y profundas. A continuación, el PDF Otros diseños, aborda diseños como el fenomenológico, etnográfico, narrativo, y estudio de caso, entre otros, explicando su propósito, características y aplicaciones prácticas. A través de ejemplos concretos, se muestra cómo estos enfoques ayudan a entender experiencias humanas, contextos culturales y dinámicas sociales complejas, aportando valiosas herramientas para la generación de conocimiento en ciencias sociales.
          a.d-flex.me-auto.bg-white.box-shadow.cursor-pointer.w-fit(data-aos="zoom-in" :href="obtenerLink('/downloads/Anexos_Otros diseños.pdf')" target="_blank")
            img.h-100(style="width: 48px" src='@/assets/componentes/pdf-icon-square.svg', alt='Texto que describa la imagen')
            p.text-small.fs-14px.my-auto.px-2 <strong>Anexo.</strong> Otros diseños
      .col-5.d-none.d-md-block(data-aos="fade-left")
        img(src='@/assets/curso/unidad/img-16.png', alt='Texto que describa la imagen')

    p.mb-4 Ahora, los instrumentos claves de la investigación cualitativa, son:

    ImagenInfografica.color-secundario.mb-5
      template(v-slot:imagen)
        figure
          img(src='@/assets/curso/unidad/img-17.png', alt='Texto que describa la imagen')

      .tarjeta.bg-amarillo-claro.p-4(x="50.4%" y="35%" numero="+")
        .h5.mb-2 Entrevistas
        p Las entrevistas son una herramienta importante en la investigación cualitativa. Los investigadores hacen preguntas abiertas que permiten a las personas compartir sus pensamientos y experiencias en detalle. Por ejemplo, un investigador podría preguntar: "¿qué significa para ti la comida en familia?" Esta pregunta invita a la persona a contar su historia.
      .tarjeta.bg-amarillo-claro.p-4(x="50.4%" y="57%" numero="+")
        .h5.mb-2 Grupos focales
        p Los grupos focales son reuniones donde varias personas discuten un tema específico. Un moderador guía la conversación, asegurándose de que todos tengan la oportunidad de compartir. Esto ayuda a los investigadores a ver diferentes puntos de vista. Por ejemplo, si se está investigando sobre juegos, un grupo focal puede incluir niños que comparten sus juegos favoritos y por qué les gustan.
      .tarjeta.bg-amarillo-claro.p-4(x="50.4%" y="79%" numero="+")
        .h5.mb-2 Observación
        p La observación es otra técnica en la investigación cualitativa. Los investigadores observan a las personas en su entorno natural. Esto les permite ver cómo actúan y se comunican sin influir en su comportamiento. Por ejemplo, un investigador podría observar cómo los niños juegan en el parque para entender mejor sus interacciones y amistades.

    h2 Ventajas y limitaciones de la investigación cualitativa
    p.mb-4 La investigación cualitativa ofrece varias ventajas. Una de ellas es la capacidad de obtener una comprensión profunda de fenómenos complejos que no pueden ser reducidos a números o estadísticas. Además, la flexibilidad del enfoque cualitativo permite la exploración de temas emergentes que no se habían considerado previamente.

    .row.mb-4
      .col-12.col-xl-7.mx-auto(data-aos="fade-up")
        p.mb-3 <strong>Figura 2.</strong> Fortalezas de la investigación cualitativa
        img(src='@/assets/curso/unidad/img-18.png', alt='Texto que describa la imagen')

    .row.mb-4
      .col-12.col-xl-7.mx-auto(data-aos="fade-up")
        p.mb-4 La investigación cualitativa es una puerta abierta a la comprensión profunda de la realidad humana. A través de ella, no solo exploramos el qué, sino el por qué y el cómo de las acciones y pensamientos humanos. Adentrarse en este mundo es una oportunidad para descubrir nuevas perspectivas, desafiar supuestos y construir conocimiento significativo para nuestra sociedad.
        .bg-amarillo.p-4 
          span Sin embargo, también presenta algunas limitaciones. Una de las más destacadas es la dificultad para generalizar los resultados obtenidos, ya que los estudios cualitativos suelen centrarse en muestras pequeñas y contextos específicos. Además, el análisis de datos cualitativos puede ser subjetivo y requiere una gran habilidad interpretativa por parte del investigador, lo que puede llevar a variaciones en los resultados dependiendo de quién realice el estudio. 
      .col-5.d-none.d-xl-block(data-aos="fade-left")
        img(src='@/assets/curso/unidad/img-19.png', alt='Texto que describa la imagen')

    #t_2_2.titulo-segundo
      div.d-flex
        span.me-3 2.2  
        h2 Investigación cuantitativa 
    
    p.mb-4 Para abordar este tipo de investigación, iniciaremos por un caso de estudio; supongamos que, al interior de un salón de clases, en ambiente virtual, queremos que se examine la relación entre el tiempo de estudio y las calificaciones de los estudiantes, en una determinada materia o asignatura. En este caso, se podrían recopilar datos sobre la cantidad de horas que los estudiantes dedican a estudiar y sus calificaciones finales. Luego, se aplicarían métodos estadísticos, como la correlación, para determinar si existe una relación significativa entre estas variables. Este es un ejemplo claro de lo que son los estudios cuantitativos.  
    
    .row.mb-4
      .d-none.d-xl-block.col-xl-5(data-aos="fade-right")
        img(src='@/assets/curso/unidad/img-20.png', alt='Texto que describa la imagen')
      .col-12.col-xl-7(data-aos="fade-left")
        .d-flex.align-items-start.bg-rojo.p-4.mb-4(data-aos="fade-left")
          img.me-3(style="width: 90px" src='@/assets/curso/unidad/img-21.svg', alt='Texto que describa la imagen')
          .d-flex.flex-column
            p.pe-xl-3.mb-0(data-aos="fade-up") La investigación cuantitativa es un enfoque metodológico que se caracteriza por su uso de datos numéricos y análisis estadísticos, para examinar fenómenos y establecer relaciones entre variables. 
        span Este planteamiento se enfoca en la recolección y análisis de datos estructurados, permitiendo a los investigadores probar hipótesis y generalizar los resultados a una población más amplia. A diferencia de la investigación cualitativa, que busca una comprensión profunda y subjetiva de los fenómenos, la investigación cuantitativa pone énfasis en la objetividad, precisión y replicabilidad.

    .row.mb-4
      .col-12.col-xl-7(data-aos="fade-right")
        p.mb-4 Puede decirse que la investigación cuantitativa es una forma de estudiar el mundo desde los números y datos. Los científicos y los investigadores utilizan esta técnica para responder preguntas y resolver problemas. 
        p.mb-4 Por ejemplo, si un investigador quiere saber cuántos estudiantes tienen mascotas. Se puede hacer una encuesta donde cada estudiante escribe el tipo de mascota que tiene. Luego, el maestro puede contar cuántos estudiantes tienen perros, gatos, peces, etc. Esto es investigación cuantitativa porque utiliza números para entender la información. Los números ayudan a los investigadores a ver patrones y tendencias en los datos.
        .bg-rojo.p-4
          span La investigación cuantitativa se utiliza ampliamente en campos como las ciencias básicas, la psicología, la sociología, la economía, la educación y las ciencias de la salud. Su enfoque riguroso y su capacidad para generar resultados generalizables, la hacen fundamental para avanzar en la comprensión de patrones y tendencias en poblaciones grandes. (Bonilla-Castro, 2005).
      .d-none.d-xl-block.col-xl-5(data-aos="fade-left")
        img(src='@/assets/curso/unidad/img-22.png', alt='Texto que describa la imagen')
    
    .row.mb-4
      .d-none.d-xl-block.col-xl-5(data-aos="fade-right")
        img(src='@/assets/curso/unidad/img-23.png', alt='Texto que describa la imagen')
      .col-12.col-xl-7(data-aos="fade-left")
        p.mb-4 Los estudios basados en esta metodología, suelen utilizar o abarcar grandes muestras, instrumentos estandarizados que permiten procesar información haciendo uso de análisis estadísticos, este enfoque permite a los investigadores probar hipótesis y generalizar resultados, lo que es crucial para la toma de decisiones basadas en evidencia.
        span Es importante resaltar que, en el campo de las ciencias sociales, nos puede ayudar a tomar decisiones basadas en hechos; por ejemplo, si un médico quiere saber si un nuevo medicamento es efectivo, puede usar investigación cuantitativa para analizar los resultados de las pruebas. Si los números muestran que el medicamento ayuda a muchas personas, el médico puede recomendarlo.

    p.mb-4 Las principales características de la investigación cuantitativa, son:

    .row.mb-5
      .col-12.col-lg-7(data-aos="fade-right")
        AcordionA(tipo="b")
          .div(titulo="Objetividad")
            p La investigación cuantitativa busca minimizar la influencia de las creencias y expectativas del investigador, en el proceso de recolección y análisis de datos, dado que se basa en hechos y no en opiniones, lo que significa que es menos probable que haya confusión sobre los resultados. Esto se logra mediante la implementación de instrumentos estandarizados y técnicas de análisis estadístico que reducen el sesgo.
          .div(titulo="Uso de muestras grandes")
            p Para garantizar que los resultados sean representativos y puedan generalizarse a una población más amplia, la investigación cuantitativa generalmente utiliza grandes muestras. Cuanto mayor sea la muestra, más probable es que los resultados reflejen con precisión las características de la población objetivo.
          .div(titulo="Instrumentos de recolección de datos estructurados")
            p Los investigadores cuantitativos emplean encuestas, cuestionarios y otros instrumentos estandarizados que permiten recolectar datos de manera sistemática y coherente. Estos instrumentos suelen incluir preguntas cerradas con opciones de respuesta predefinidas, lo que facilita la cuantificación de las respuestas.
          .div(titulo="Análisis estadístico")
            p El análisis estadístico es el núcleo de la investigación cuantitativa. A través de técnicas como la prueba t de Student o Test-t, el análisis de regresión y el análisis de varianza (ANOVA), los investigadores pueden identificar relaciones entre variables, probar hipótesis y medir la significancia estadística de los resultados.
          .div(titulo="Replicabilidad")
            p.mb-4 Un aspecto clave de la investigación cuantitativa, es su capacidad de ser replicada. Esto significa que otros investigadores, utilizando los mismos métodos y muestras similares, deberían obtener resultados comparables. La replicabilidad es esencial para validar los hallazgos y fortalecer la credibilidad de los estudios.
            p.mb-4 ●  Claridad: los números hacen que la información sea más clara. Es más fácil ver que 75 de 100 estudiantes prefieren un tipo de comida que solo escuchar una opinión.
            p.mb-4 ●  Comparaciones: los números permiten hacer comparaciones. Por ejemplo, se puede comparar el rendimiento de dos grupos diferentes en una prueba.
      .col-12.col-lg-5.d-none.d-lg-block(data-aos="fade-left")
        img(src='@/assets/curso/unidad/img-24.png', alt='Texto que describa la imagen')

    .row.mb-4
      .col-5.d-none.d-md-block(data-aos="fade-right")
        img(src='@/assets/curso/unidad/img-25.png', alt='Texto que describa la imagen')
      .col-12.col-xl-7(data-aos="fade-left")
        h2  Tipos de diseño en la investigación cuantitativa
        .bg-amarillo.p-4
          p.mb-4 Los diseños de investigación cuantitativa ofrecen un marco estructurado y riguroso para analizar datos numéricos, permitiendo explorar fenómenos sociales, relaciones entre variables y establecer causalidades. A continuación, el PDF Tipos de diseño en la investigación cuantitativa, aborda diversos diseños como los descriptivos, correlacionales, experimentales y longitudinales, explicando sus características, aplicaciones y variaciones. A través de ejemplos concretos, se profundiza en cómo estos enfoques ayudan a responder preguntas de investigación y a generar conocimiento basado en evidencia objetiva.
          a.d-flex.me-auto.bg-white.box-shadow.cursor-pointer.w-fit(data-aos="zoom-in" :href="obtenerLink('/downloads/Anexos_Tipos de diseño en la investigación cuantitativa.pdf')" target="_blank")
            img.h-100(style="width: 48px" src='@/assets/componentes/pdf-icon-square.svg', alt='Texto que describa la imagen')
            p.text-small.fs-14px.my-auto.px-2 <strong>Anexo.</strong> Tipos de diseño en la investigación cuantitativa 

    .bg-fondo-1.bg-full-width
      .px-5.pb-md-3.py-4

        h2 Etapas del proceso de investigación cuantitativa
        p.mb-4  El proceso de investigación cuantitativa, sigue una serie de etapas bien definidas que aseguran la coherencia y validez del estudio. Estas etapas incluyen:

        .row.mb-4
          .col-12.col-lg-10.mx-auto(data-aos="fade-up")
            SlyderF(columnas="col-12 .col-md-6 col-lg-4 py-4 px-3")
              div.custom-image-card.rounded-8px.py-4
                img.mx-auto.mb-4(src='@/assets/curso/unidad/img-26.svg', style="width: 80px")
                p.text-bold.mb-4.text-center Formulación del problema de investigación
                p.px-1.text-small.mb-0.d-flex.text-center El primer paso es definir claramente el problema o la pregunta de investigación. Esto implica identificar qué se quiere estudiar y por qué es importante.
              div.custom-image-card.rounded-8px.py-4
                img.mx-auto.mb-4(src='@/assets/curso/unidad/img-27.svg', style="width: 80px")
                p.text-bold.mb-4.text-center Revisión de la literatura
                p.px-1.text-small.mb-0.d-flex.text-center Una vez definido el problema, es crucial revisar investigaciones previas que puedan proporcionar un marco teórico para el estudio. Esto permite identificar brechas en el conocimiento existente y refinar el enfoque de la investigación.
              div.custom-image-card.rounded-8px.py-4
                img.mx-auto.mb-4(src='@/assets/curso/unidad/img-28.svg', style="width: 80px")
                p.text-bold.mb-4.text-center Formulación de hipótesis
                p.px-1.text-small.mb-0.d-flex.text-center En estudios cuantitativos, las hipótesis son formulaciones tentativas sobre las relaciones entre variables. Estas hipótesis deben ser claras, precisas y comprobables a través de métodos estadísticos.
              div.custom-image-card.rounded-8px.py-4
                img.mx-auto.mb-4(src='@/assets/curso/unidad/img-29.svg', style="width: 80px")
                p.text-bold.mb-4.text-center Recolección de datos
                p.px-1.text-small.mb-0.d-flex.text-center El siguiente paso es seleccionar un diseño de investigación adecuado y recolectar datos utilizando instrumentos estandarizados como encuestas, cuestionarios o mediciones experimentales.
              div.custom-image-card.rounded-8px.py-4
                img.mx-auto.mb-4(src='@/assets/curso/unidad/img-30.svg', style="width: 80px")
                p.text-bold.mb-4.text-center Análisis de datos
                p.px-1.text-small.mb-0.d-flex.text-center Una vez recolectados los datos, se utilizan técnicas estadísticas para analizar los resultados y determinar si las hipótesis planteadas son apoyadas o refutadas por los datos.
              div.custom-image-card.rounded-8px.py-4
                img.mx-auto.mb-4(src='@/assets/curso/unidad/img-31.svg', style="width: 80px")
                p.text-bold.mb-4.text-center Interpretación y presentación <br> de resultados
                p.px-1.text-small.mb-0.d-flex.text-center Los resultados del análisis estadístico se interpretan en función de las hipótesis originales y se discuten en el contexto de la literatura revisada. Finalmente, los hallazgos se presentan de manera clara y estructurada.

    h2 Ventajas y limitaciones de la investigación cuantitativa
    p.mb-4 La investigación cuantitativa es fundamental para las ciencias sociales y otras disciplinas, debido a su capacidad para producir resultados generalizables y precisos. Los estudios cuantitativos permiten a los investigadores identificar patrones y tendencias a gran escala, lo que es esencial para la formulación de políticas públicas, el desarrollo de intervenciones sociales y la mejora de prácticas profesionales.

    .row.mb-4
      .col-12.col-xl-7(data-aos="fade-left")
        .d-flex.align-items-start.bg-rojo.p-4.mb-4(data-aos="fade-right")
          img.me-3(style="width: 90px" src='@/assets/curso/unidad/img-32.svg', alt='Texto que describa la imagen')
          .d-flex.flex-column
            p.pe-xl-3.mb-0(data-aos="fade-up") Este tipo de investigación es crucial en la validación de teorías y modelos. A través de la prueba empírica, los investigadores pueden confirmar o refutar supuestos teóricos, lo que contribuye al avance del conocimiento en diversos campos. 
        p.mb-4 A pesar de sus ventajas, la investigación cuantitativa también tiene limitaciones. En primer lugar, su enfoque en la objetividad y la medición numérica puede pasar por alto los matices y la complejidad de las experiencias humanas.
        p.mb-4 Los estudios cuantitativos tienden a simplificar los fenómenos, lo que puede llevar a una comprensión superficial en algunos casos.
        p.mb-4 Además, la investigación cuantitativa puede ser costosa y llevar mucho tiempo, especialmente cuando se requieren grandes muestras para obtener resultados generalizables. La necesidad de instrumentos estandarizados también puede limitar la flexibilidad del investigador para adaptarse a contextos cambiantes o situaciones imprevistas durante la recolección de datos.
      .d-none.d-xl-block.col-xl-5(data-aos="fade-left")
        img(src='@/assets/curso/unidad/img-33.png', alt='Texto que describa la imagen')

    #t_2_3.titulo-segundo
      div.d-flex
        span.me-3 2.3  
        h2 Investigación mixta
    p.mb-4 Para introducirnos a este tema, partiremos de una situación real, desde el Departamento Administrativo Nacional de Estadística – DANE; se está analizando a profundidad el fenómeno del desempleo en Colombia en la última década y para hacerlo, se analizan las diferentes tasas históricas de desempleo entre grupos variados de personas, durante dicho periodo, mientras que también se realizan entrevistas con personas desempleadas para explorar sus experiencias y percepciones de la discriminación en el mercado laboral. Esto puede considerarse, una investigación mixta.

    .row.mb-4
      .col-5.d-none.d-xl-block(data-aos="fade-right")
        img(src='@/assets/curso/unidad/img-34.png', alt='Texto que describa la imagen')
      .col-12.col-xl-7(data-aos="fade-left")
        p.mb-4 La investigación de enfoque mixto es una metodología que combina elementos de la investigación cualitativa y cuantitativa, con el fin de aprovechar las fortalezas de ambos enfoques y superar sus limitaciones. Este enfoque se basa en la idea que la integración de datos numéricos y narrativos, permite una comprensión más completa de los fenómenos sociales y científicos. En lugar de centrarse únicamente en estadísticas o en interpretaciones subjetivas, los investigadores de enfoque mixto, integran diversas fuentes de datos para obtener una visión más holística y profunda de la realidad que estudian.
        .bg-rojo.p-4
          span El enfoque mixto es especialmente útil cuando el problema de investigación es complejo y multifacético, y cuando un solo enfoque no es suficiente para capturar todas las dimensiones relevantes del fenómeno estudiado. Esta metodología permite a los investigadores validar y enriquecer sus hallazgos mediante la triangulación de datos provenientes de diferentes fuentes y métodos. (Ballesteros, et al., 2024)

    .bg-fondo-1.bg-full-width
      .px-5.pb-md-3.py-4

        h2 Principales características de la investigación de enfoque mixto
        p.mb-4 La investigación mixta no es un sustituto de la investigación cualitativa ni de la cuantitativa, solo busca minimizar las debilidades que, por separado, cada una de estas presenta y aprovecha sus fortalezas.
        p.mb-4 Las características de la metodología mixta, son:

        .row.mb-4
          .col-12.col-lg-10.mx-auto(data-aos="fade-up")
            SlyderF(columnas="col-12 .col-md-6 col-lg-4 py-4 px-3")
              div.custom-image-card.rounded-8px.py-4
                img.mx-auto.mb-4(src='@/assets/curso/unidad/img-35.svg', style="width: 80px")
                p.text-bold.mb-4.text-center Objetivo
                p.px-1.text-small.mb-0.d-flex.text-center Busca lograr una perspectiva más amplia y profunda del fenómeno. 
              div.custom-image-card.rounded-8px.py-4
                img.mx-auto.mb-4(src='@/assets/curso/unidad/img-36.svg', style="width: 80px")
                p.text-bold.mb-4.text-center Problema
                p.px-1.text-small.mb-0.d-flex.text-center El planteamiento del problema es mucho más claro. 
              div.custom-image-card.rounded-8px.py-4
                img.mx-auto.mb-4(src='@/assets/curso/unidad/img-37.svg', style="width: 80px")
                p.text-bold.mb-4.text-center Trabajo de campo
                p.px-1.text-small.mb-0.d-flex.text-center Efectúa indagaciones más dinámicas debido a la adaptación a las condiciones de estudio.
              div.custom-image-card.rounded-8px.py-4
                img.mx-auto.mb-4(src='@/assets/curso/unidad/img-38.svg', style="width: 80px")
                p.text-bold.mb-4.text-center Sustento
                p.px-1.text-small.mb-0.d-flex.text-center Impulsa la creatividad teórica por medio de diferentes técnicas y metodologías.  
              div.custom-image-card.rounded-8px.py-4
                img.mx-auto.mb-4(src='@/assets/curso/unidad/img-39.svg', style="width: 80px")
                p.text-bold.mb-4.text-center Resultado
                p.px-1.text-small.mb-0.d-flex.text-center Arroja resultados más ricos y variados, debido a la multiplicidad de datos y observaciones. 

    p.mb-4 Otras características son las siguientes: 

    .bg-verde.bg-full-width.mb-5
      .px-5.pb-md-3.py-4

        SlyderA(tipo="b").bg-white
          .row.p-4.px-md-5.py-lg-5.justify-content-between
            .col-md-12.col-lg-7.mb-4.mb-md-0
              p.text-bold Integración de datos cualitativos y cuantitativos
              p La principal característica del enfoque mixto es la combinación de datos numéricos, que suelen provenir de encuestas o análisis estadísticos, y datos cualitativos, que pueden incluir entrevistas, observaciones y grupos focales. Esta integración puede realizarse en distintas fases del estudio, ya sea de forma simultánea o secuencial.
            .d-none.d-lg-block.col-md-6.col-lg-4
              figure
                img(src='@/assets/curso/unidad/img-40.png', alt='Texto que describa la imagen')
          .row.p-4.px-md-5.py-lg-5.justify-content-between
            .col-md-12.col-lg-7.mb-4.mb-md-0
              p.text-bold Triangulación de datos
              p Uno de los propósitos más importantes del enfoque mixto es la triangulación, que permite al investigador contrastar y comparar resultados obtenidos a partir de métodos distintos. La triangulación mejora la validez de los hallazgos al reducir el sesgo que puede surgir al depender de una sola fuente de datos o un solo método.
            .d-none.d-lg-block.col-md-6.col-lg-4
              figure
                img(src='@/assets/curso/unidad/img-41.png', alt='Texto que describa la imagen')
          .row.p-4.px-md-5.py-lg-5.justify-content-between
            .col-md-12.col-lg-7.mb-4.mb-md-0
              p.text-bold Flexibilidad metodológica
              p A diferencia de los enfoques tradicionales, la investigación de enfoque mixto es flexible y adaptable. Los investigadores pueden diseñar sus estudios para que se ajusten mejor a las características del problema de investigación, el contexto y los participantes. Esto permite modificar el diseño a medida que se avanza en la recolección y análisis de datos.
            .d-none.d-lg-block.col-md-6.col-lg-4
              figure
                img(src='@/assets/curso/unidad/img-42.png', alt='Texto que describa la imagen')
          .row.p-4.px-md-5.py-lg-5.justify-content-between
            .col-md-12.col-lg-7.mb-4.mb-md-0
              p.text-bold Complementariedad de los métodos
              p La investigación de enfoque mixto utiliza los métodos cualitativos y cuantitativos de manera complementaria, donde los hallazgos de un enfoque pueden enriquecer o explicar los resultados del otro. Por ejemplo, los datos cuantitativos pueden identificar tendencias generales, mientras que los cualitativos pueden proporcionar explicaciones más profundas de por qué ocurren estas tendencias.
            .d-none.d-lg-block.col-md-6.col-lg-4
              figure
                img(src='@/assets/curso/unidad/img-43.png', alt='Texto que describa la imagen')
    
    h2 Tipos de diseños en la investigación mixta
    p.mb-4 Dentro de la investigación de enfoque mixto, existen varios tipos de diseños que permiten organizar la manera en que los métodos cualitativos y cuantitativos interactúan. Los más comunes incluyen:

    .row.mb-5
      .col-12.col-lg-5.d-none.d-lg-block(data-aos="fade-right")
        img(src='@/assets/curso/unidad/img-44.png', alt='Texto que describa la imagen')
      .col-12.col-lg-7(data-aos="fade-left")
        AcordionA(tipo="b")
          .div(titulo="Diseño de triangulación concurrente")
            p En este diseño, los datos cualitativos y cuantitativos se recogen al mismo tiempo y se analizan de manera independiente, pero los resultados se combinan en la fase de interpretación. Este diseño busca contrastar los hallazgos de ambos enfoques para validar los resultados obtenidos.
          .div(titulo="Diseño secuencial explicativo")
            p Este diseño comienza con la recolección de datos cuantitativos y luego sigue con un análisis cualitativo para explicar los resultados obtenidos. Por ejemplo, después de realizar una encuesta y analizar los datos numéricos, un investigador podría llevar a cabo entrevistas para profundizar en los resultados y entender mejor los factores que subyacen a los patrones observados.
          .div(titulo="Diseño secuencial exploratorio")
            p A diferencia del diseño explicativo, en el diseño secuencial exploratorio, la investigación cualitativa se realiza primero y los datos cuantitativos se recopilan después para probar o expandir los hallazgos cualitativos. Este diseño es útil cuando el tema es poco conocido o cuando se quiere desarrollar un instrumento de medición basado en los datos cualitativos iniciales.
          .div(titulo="Diseño embebido")
            p En este diseño, uno de los enfoques, cualitativo o cuantitativo, desempeña un papel secundario dentro del estudio. Por ejemplo, un estudio cuantitativo puede incorporar algunos datos cualitativos para enriquecer ciertos aspectos, o viceversa. Este enfoque es útil cuando un único método no puede cubrir completamente todos los aspectos del problema de investigación.

    h2 Ventajas del enfoque mixto
    p.mb-4 El enfoque mixto ofrece una serie de ventajas importantes en comparación con los enfoques puramente cualitativos o cuantitativos:
    .row.mb-5
      .col-12.col-lg-7(data-aos="fade-right")
        AcordionA(tipo="b")
          .div(titulo="Mayor comprensión de los fenómenos complejos")
            p La combinación de métodos cualitativos y cuantitativos, permite a los investigadores abordar problemas complejos desde múltiples ángulos. Los datos cuantitativos pueden proporcionar una visión amplia y generalizable, mientras que los cualitativos ofrecen un entendimiento profundo de las percepciones y experiencias individuales. 
          .div(titulo="Validación cruzada")
            p Mediante la triangulación de datos, los investigadores pueden validar sus resultados, lo que aumenta la confiabilidad de los hallazgos. Si los resultados de los métodos cualitativos y cuantitativos coinciden, esto proporciona mayor evidencia de que las conclusiones son sólidas.
          .div(titulo="Flexibilidad")
            p El enfoque mixto permite adaptar el diseño de investigación a las necesidades específicas del estudio. Esto lo hace especialmente adecuado para investigaciones en ciencias sociales, donde los fenómenos estudiados suelen ser multidimensionales y dinámicos. Si un método no está proporcionando la información necesaria, pueden cambiar a otro método para obtener mejores resultados.
          .div(titulo="Respuesta a preguntas múltiples")
            p Mientras que los métodos cuantitativos suelen ser más adecuados para responder preguntas sobre el "qué" y el "cuánto", los métodos cualitativos se centran en el "cómo" y el "por qué". El enfoque mixto permite a los investigadores abordar un espectro más amplio de preguntas dentro de un solo estudio.
          .div(titulo="Perspectiva completa")
            p Al combinar métodos cualitativos y cuantitativos, los investigadores pueden obtener una visión más completa de un problema. Por ejemplo, si solo se utiliza una encuesta, se pueden perder detalles importantes que se pueden obtener a través de entrevistas.
          .div(titulo="Validación de resultados")
            p Al comparar los resultados de ambos métodos, los investigadores pueden validar sus hallazgos. Si tanto los datos numéricos como las opiniones cualitativas apuntan a la misma conclusión, esto fortalece la confianza en los resultados.
      .col-12.col-lg-5.d-none.d-lg-block(data-aos="fade-left")
        img(src='@/assets/curso/unidad/img-45.png', alt='Texto que describa la imagen')

    h2 Limitaciones del enfoque mixto
    p.mb-4 A pesar de sus numerosas ventajas, el enfoque mixto también presenta desafíos y limitaciones que los investigadores deben considerar:

    .bg-verde.bg-full-width.mb-5
      .px-5.pb-md-3.py-4

        SlyderA(tipo="b").bg-white
          .row.p-4.px-md-5.py-lg-5.justify-content-between
            .d-none.d-lg-block.col-md-6.col-lg-4
              figure
                img(src='@/assets/curso/unidad/img-46.png', alt='Texto que describa la imagen')
            .col-md-12.col-lg-7.mb-4.mb-md-0
              p.text-bold Mayor demanda de recursos
              p La investigación de enfoque mixto, requiere más tiempo, esfuerzo y recursos que los estudios puramente cualitativos o cuantitativos. Esto se debe a la necesidad de manejar dos conjuntos de datos distintos y realizar análisis complejos para integrar los resultados.
          .row.p-4.px-md-5.py-lg-5.justify-content-between
            .d-none.d-lg-block.col-md-6.col-lg-4
              figure
                img(src='@/assets/curso/unidad/img-47.png', alt='Texto que describa la imagen')
            .col-md-12.col-lg-7.mb-4.mb-md-0
              p.text-bold Desafíos en la integración de datos
              p Combinar datos cualitativos y cuantitativos no siempre es fácil. Los dos tipos de datos provienen de diferentes paradigmas y requieren distintas técnicas de análisis. La integración de los resultados puede ser un desafío metodológico importante que requiere experiencia y habilidad por parte del investigador.
          .row.p-4.px-md-5.py-lg-5.justify-content-between
            .d-none.d-lg-block.col-md-6.col-lg-4
              figure
                img(src='@/assets/curso/unidad/img-48.png', alt='Texto que describa la imagen')
            .col-md-12.col-lg-7.mb-4.mb-md-0
              p.text-bold Competencia técnica
              p Los investigadores que utilizan el enfoque mixto deben ser competentes tanto en técnicas cuantitativas como cualitativas. Esto implica una mayor inversión en formación y preparación, ya que cada enfoque tiene sus propias herramientas, métodos y requisitos.

    h2 Aplicaciones del enfoque mixto en las ciencias sociales
    p.mb-4 El enfoque mixto es particularmente útil en las ciencias sociales, debido a la naturaleza compleja y multifacética de los problemas estudiados en este campo. Algunas de las aplicaciones más comunes incluyen:

    .row.mb-5.justify-content-center 
      .col-10.col-md-8.col-lg-6.col-xl-4.mb-4.mb-xl-0
        .custom-image-card.d-flex.flex-column.p-4.py-md-5.h-100
          img.mx-auto.mb-4(src='@/assets/curso/unidad/img-49.svg', style="width: 80px")
          p.text-bold.mb-4.text-center Educación
          p.px-1.text-small.mb-0.d-flex.text-center En investigaciones sobre la efectividad de intervenciones educativas, los investigadores pueden utilizar encuestas para medir el rendimiento académico y complementar estos datos con entrevistas a estudiantes y profesores para obtener una comprensión más profunda de los factores que influyen en el aprendizaje. 
      .col-10.col-md-8.col-lg-6.col-xl-4.mb-4.mb-xl-0
        .custom-image-card.d-flex.flex-column.p-4.py-md-5.h-100
          img.mx-auto.mb-4(src='@/assets/curso/unidad/img-50.svg', style="width: 80px")
          p.text-bold.mb-4.text-center Salud pública
          p.px-1.text-small.mb-0.d-flex.text-center En estudios sobre la adopción de comportamientos saludables, los investigadores pueden combinar estadísticas sobre la prevalencia de ciertos comportamientos con entrevistas a los participantes para explorar las barreras y motivaciones detrás de sus decisiones.
      .col-10.col-md-8.col-lg-6.col-xl-4
        .custom-image-card.d-flex.flex-column.p-4.py-md-5.h-100
          img.mx-auto.mb-4(src='@/assets/curso/unidad/img-51.svg', style="width: 80px")
          p.text-bold.mb-4.text-center Sociología
          p.px-1.text-small.mb-0.d-flex.text-center En investigaciones sobre desigualdades sociales, los datos cuantitativos pueden ofrecer información sobre las diferencias de ingresos entre distintos grupos, mientras que los datos cualitativos pueden proporcionar relatos personales sobre las experiencias vividas por las personas en situación de pobreza.

    h2 Ejemplo de investigación de enfoque mixto
    p.mb-4 Imaginemos que un grupo de estudiantes quiere investigar por qué los jóvenes leen menos libros hoy en día. Podrían usar un enfoque mixto de la siguiente manera:

    .row.mb-5
      .col-12.col-lg-7(data-aos="fade-right")
        AcordionA(tipo="b")
          .div(titulo="Encuesta")
            p Primero, los estudiantes podrían crear una encuesta para preguntar a otros jóvenes cuánto tiempo pasan leyendo cada semana y qué tipo de libros prefieren. Esto les daría datos cuantitativos sobre los hábitos de lectura.
          .div(titulo="Entrevistas")
            p Luego, podrían realizar entrevistas con algunos jóvenes para explorar sus motivos. Podrían preguntarles qué les impide leer más o qué piensan sobre los libros en comparación con los videojuegos o las redes sociales. Esto les proporcionaría datos cualitativos.
          .div(titulo="Análisis de resultados")
            p Después de recoger ambos tipos de datos, los estudiantes analizarían los resultados. Podrían encontrar que muchos jóvenes dicen que prefieren leer en su teléfono o que tienen poco tiempo, lo que podría explicar el tiempo limitado que dedican a los libros.
      .col-12.col-lg-5.d-none.d-lg-block(data-aos="fade-left")
        img(src='@/assets/curso/unidad/img-52.png', alt='Texto que describa la imagen')

    .bg-full-width.border-top-9-azul
      .px-4.px-md-5.pt-5.pb-0
        h2 Material complementario
        .row
          .col-12.col-md-6.col-lg-8
            p.d-flex.my-4
              img.me-3.mb-auto.mt-1(src='@/assets/componentes/link.svg' :style="{'max-width':'16px'}")
              a.text-small.text-primary(target="_blank", href="https://elibro.net/es/lc/tecnologicadeloriente/titulos/34009") Ruiz Olabuénaga, J. I. (2013). Metodología de la investigación cualitativa: (5 ed.). Publicaciones de la Universidad de Deusto. 
            p.d-flex.my-4
              img.me-3.mb-auto.mt-1(src='@/assets/componentes/link.svg' :style="{'max-width':'16px'}")
              a.text-small.text-primary(target="_blank", href="https://elibro.net/es/lc/tecnologicadeloriente/titulos/5505 ") Plata Caviedes, J. C. (2009). Investigación cualitativa y cuantitativa: una revisión del qué y el cómo para acumular conocimiento sobre lo social: ( ed.)
            p.d-flex.my-4
              img.me-3.mb-auto.mt-1(src='@/assets/componentes/link.svg' :style="{'max-width':'16px'}")
